<template>
	<view>
		<!-- <navigator class="order_li" :url="'/pages/member/order_detail?order_id='+orderId"> -->
		<view class="order_li">
			<image lazy-load class="img scale11" :src='orderDetail.skuUrl' mode="aspectFill"></image>
			<view class="detail">
				<view class="title">{{orderDetail.skuName}}</view>
				<view class="types">{{orderDetail.specNames}}</view>
				<view class="bottom">
					<view class="money">
						 <text class="icon" v-if="!$common.isAorC(orderType)">￥</text>
						{{orderDetail.orginPrice | formatNum}}
						<text class="icon" v-if="$common.isAorC(orderType)">{{money_text[orderType]}}</text>
					</view>
					<view class="num">{{orderDetail.sl}}</view>
				</view>
			</view>
		</view>
	</view>
	
</template>

<script>
	export default {
		data(){
			return {
				money_text:{
					'ACTIVE_POINT':'积分',
					'COUPON':'团批劵',
					},
			}
		},
		props:{
			orderDetail: {
			    type: Object,
			    default() {
			        return {};
			    }
			},
			orderType:{
				type: String,
				default:"NORMAL"
			},
			orderId:{
				type:String,
				default:0
			}
		}
	}
</script>

<style lang="scss">
	.order_li {
		width: 100%;
		display: flex;
		justify-content: space-between;
		margin-bottom: 30upx;
		cursor: pointer;
		.img {
			width:167upx;
			height:167upx;
			border-radius:7upx;
			margin-right: 20upx;
			flex-shrink: 0;
			background: #eee;
		}
		.detail {
			display: flex;
			flex-wrap: wrap;
			flex-grow: 1;
			align-content: flex-start;
			.title {
				width: 100%;
				font-size:27upx;
				font-weight:800;
				color:rgba(51,51,51,1);
